export default {
	data() {
		return {
			providerList: []
		}			
	},
	created() {
		this.getAuthLogin()
	},
	methods: {
		getAuthLogin() {
			uni.getProvider({
				service: 'oauth',
				success: (result) => {
					this.providerList = result.provider.map((value, index) => {
						let providerName = '';
						let icon = ''
						let iconClass = ''
						switch (value) {
							case 'weixin':
								providerName = '微信登录'
								icon = 'weixin'
								iconClass = 'wx'
								break;
							case 'qq':
								providerName = 'QQ登录'
								icon = 'QQ'
								iconClass = 'qq'
								break;
							case 'sinaweibo':
								providerName = '新浪微博登录'
								icon = 'xinlangweibo'
								iconClass = 'wb'
								break;						
						}							
						return {
							name: providerName,
							id: value,
							icon,
							iconClass
						}
					});						
					
					this.providerList = this.providerList.filter(item => {
						return item.name !== ''
					})
			
				},
				fail: (error) => {
					console.log('获取登录通道失败', error);
				}
			});
		},
		tologin(provider) {
			uni.login({
				provider: provider.id,



				success: (res) => {
					console.log('login success:', res);
					// 更新保存在 store 中的登录状态
					// this.login(provider.id);
					uni.showToast({
						title: '登录成功',
						icon: 'success'
					})
				},
				fail: (err) => {
					console.log('login fail:', err);
				}
			});
		}
	}
}
